import React from 'react';
import PropTypes from 'prop-types';
import { flattenObj } from '../../../../util';

/**
 * Renders standard pagination controls patttern for the results.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Pagination(props) {
    const numberOfPages = Math.ceil(props.totalMatching / props.numRanks),
        currentPage = Math.ceil(props.currStart / props.numRanks),
        pages = [];

    const changePage = (pageNumber) => {
        const newStartRank = 1 + (pageNumber - 1) * props.numRanks,
            newQuery = props.query;

        newQuery.startRank = newStartRank;
        newQuery.misspelling = null;
        newQuery.interacted = true;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const hyperLink = (pageNumber) => {
        const newStartRank = 1 + (pageNumber - 1) * props.numRanks,
            newQuery = flattenObj(props.query);

        const paramQueryString = newQuery
            ? Object.entries(newQuery)
                  .filter((key) => {
                      let param = Object.values(key);

                      if (param[1] === '') {
                          return false;
                      } else {
                          switch (param[0]) {
                              case 'interacted':
                                  return false;
                              case 'num_rank':
                                  return false;
                              case 'start_rank':
                                  return false;
                              default:
                                  return true;
                          }
                      }
                  })
                  .map((key) => {
                      let param = Object.values(key);

                      if (/fixedParameters/.test(param[0])) {
                          return (
                              encodeURIComponent(param[0].substring(16)) +
                              '=' +
                              encodeURIComponent(param[1])
                          );
                      } else if (/fixedFacets/.test(param[0])) {
                          return (
                              encodeURIComponent(
                                  `meta_${param[0].substring(12)}_sand`
                              ) +
                              '=' +
                              encodeURIComponent(param[1])
                          );
                      } else if (/parameters/.test(param[0])) {
                          return (
                              encodeURIComponent(param[0].substring(11)) +
                              '=' +
                              encodeURIComponent(param[1])
                          );
                      }
                      switch (param[0]) {
                          case 'collection':
                              return (
                                  encodeURIComponent(param[0]) +
                                  '=' +
                                  encodeURIComponent(param[1])
                              );
                          case 'query':
                              return (
                                  encodeURIComponent(param[0]) +
                                  '=' +
                                  encodeURIComponent(param[1])
                              );
                          case 'sortType':
                              return (
                                  encodeURIComponent('sort') +
                                  '=' +
                                  encodeURIComponent(param[1])
                              );
                          case 'startRank':
                              return (
                                  encodeURIComponent('start_rank') +
                                  '=' +
                                  encodeURIComponent(newStartRank)
                              );
                          case 'numRanks':
                              return (
                                  encodeURIComponent('num_rank') +
                                  '=' +
                                  encodeURIComponent(param[1])
                              );
                          default:
                              return (
                                  encodeURIComponent(`meta_${param[0]}_sand`) +
                                  '=' +
                                  encodeURIComponent(param[1])
                              );
                      }
                  })
                  .join('&')
            : '';
        return '?' + paramQueryString;
    };

    pages.push(
        currentPage === 1 ? (
            <span
                key="previousPage"
                className="pagination__controls__button--prev"
            >
                <span>Previous page</span>
            </span>
        ) : (
            <a
                className="pagination__controls__button--prev"
                key="prev"
                onClick={(e) => {
                    e.preventDefault();
                    changePage(currentPage - 1);
                }}
                href={hyperLink(currentPage - 1)}
            >
                <span>Previous page {currentPage}</span>
            </a>
        )
    );

    for (let page = 1; page <= numberOfPages; page++) {
        let className;
        switch (page - numberOfPages) {
            case 0:
                className =
                    'pagination__controls__element pagination__controls__button pagination__controls__button--last';
                break;
            case -1:
                className =
                    'pagination__controls__element pagination__controls__button pagination__controls__button--penultimate';
                break;
            default:
                className =
                    'pagination__controls__element pagination__controls__button';
        }

        pages.push(
            <a
                aria-current={page === currentPage ? 'page' : null}
                aria-expanded={page === currentPage ? true : false}
                aria-label={`Open page ${page}`}
                className={className}
                data-page={page}
                data-proximity={Math.abs(page - currentPage)}
                disabled={page === currentPage ? true : false}
                key={page}
                onClick={(e) => {
                    e.preventDefault();
                    changePage(page);
                }}
                href={hyperLink(page)}
            >
                <span>{page}</span>
            </a>
        );

        if (page === 1) {
            pages.push(
                <span
                    key="firstEllipsis"
                    className="pagination__controls__element pagination__controls__ellipsis pagination__controls__ellipsis--first"
                >
                    <span>…</span>
                </span>
            );
        } else if (page === numberOfPages - 1) {
            pages.push(
                <span
                    key="lastEllipsis"
                    className="pagination__controls__element pagination__controls__ellipsis pagination__controls__ellipsis--last"
                >
                    <span>…</span>
                </span>
            );
        }
    }

    pages.push(
        currentPage === numberOfPages ? (
            <span key="nextPage" className="pagination__controls__button--next">
                <span>Next page</span>
            </span>
        ) : (
            <a
                className="pagination__controls__button--next"
                key="next"
                disabled={currentPage === numberOfPages ? true : false}
                onClick={(e) => {
                    e.preventDefault();
                    changePage(currentPage + 1);
                }}
                href={hyperLink(currentPage + 1)}
            >
                <span>Next page</span>
            </a>
        )
    );

    return (
        <div className="pagination__wrapper">
            <nav
                className="pagination__controls"
                data-pagecount={numberOfPages}
            >
                {pages}
            </nav>
        </div>
    );
}

Finder__Pagination.propTypes = {
    currStart: PropTypes.number,
    query: PropTypes.object,
    numRanks: PropTypes.number,
    totalMatching: PropTypes.number,
    update: PropTypes.object,
};

export default Finder__Pagination;
